@import "variables";
@import "fonts";
@import "libs/normalize";

@import "components/all";

body {
	background-color: #fff;
	font-family: "Roboto";
	color: #111827;
}

.container {
	max-width: $container-width;
	padding: 0px 15px;
	margin: 0 auto;
}

// Добавить потом красивую анимацию подчёркивание через отдельный класс

a {
	&:hover {
		text-decoration: underline;
	}
}

#root {
	padding-bottom: 50px;
	scrollbar-width: none;
}

body::-webkit-scrollbar {
	width: 0; /* For Chrome, Safari, Opera */
}
