.header-time-menu {
    transition: 0.25s ease-in-out all;
    position: absolute;

    &._hide {
        opacity: 0;
        z-index: -1;
        transform: translateY(-20px);
    }
    &__content {
        box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.05);
        padding: 15px;
        border-radius: 15px;
        transform: translateY(10px);
    }
    &__list {
        display: flex;
        flex-direction: column;
        gap: 5px;
    }
    &__item {
        &._right {
            text-align: right;
        }
        &._time {
            width: 144px;
        }
        &._text {
            margin-top: 10px;
        }
        &._open {
            color: #16ab4d;
            font-weight: 500;
        }
        &._close {
            color: #c82020;
            font-weight: 500;
        }
    }
}
