.header {
	padding: 20px 0;
	&__content {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__right {
		display: flex;
		justify-content: space-between;
		align-items: center;
		gap: 50px;
	}
	&__worktime-time-arrow {
		transition: 0.25s ease-in-out all;
		&._hide {
			transform: rotate(270deg);
		}
	}
	&__worktime-tel {
		font-size: 18px;
		font-weight: 700;
		color: #4b5258;
	}
	&__worktime-time {
		cursor: pointer;

		&._open span::before {
			background-color: #16ab4d;
		}
		span {
			font-size: 14px;
			color: #959a9d;
			text-decoration: underline;

			position: relative;
			left: 15px;

			&::before {
				content: "";
				width: 8px;
				height: 8px;
				background-color: #c62429;
				border-radius: 50%;

				position: absolute;
				top: 50%;
				left: -13px;
				transform: translateY(-50%);
			}
		}

		svg {
			position: relative;
			right: -23px;
			top: 3px;
			transform: rotate(-90deg);
		}
	}
	&__social {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 20px;
	}
	&__social-icons {
		display: flex;
		gap: 15px;
	}
	&__social-text {
		font-size: 14px;
		line-height: 140%;
		text-align: right;
	}
	&__social-link {
		display: flex;
		justify-content: center;
		align-items: center;

		width: 50px;
		height: 50px;
		text-align: center;
		text-decoration: none;
		color: #000;
		box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.05);
		border-radius: 50%;
		position: relative;
		overflow: hidden;
		transition: transform 0.5s;

		svg {
			transition: 0.5s all;
		}

		&._inst {
			&::after {
				background: linear-gradient(-45deg, #ed1c94, #ffec17);
			}
		}

		&::after {
			content: "";
			width: 100%;
			height: 100%;
			top: -90px;
			left: 0;
			z-index: -1;
			background: #000;
			position: absolute;
			transition: 0.5s;
			background: linear-gradient(45deg, #25d366, #075e54);
		}

		@media (any-hover: hover) {
			&:hover {
				fill: #fff;
				&::after {
					top: 0;
				}
			}
		}
	}
}
